body {
  background-color: dodgerblue;
}

.App {
  background-color: #333;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form {
  background-color: #adadad;
  padding: 30px;
}

input {
  padding: 10px;
  border: 0;
  text-transform: capitalize;
  font-weight: bolder;
  margin-left: 24px;
}

button {
  background-color: dodgerblue;
  padding: 10px 30px;
  border: none;
  color: #fff;
  margin-left: 10px;
  cursor: pointer;
}

button:hover{
  background-color: #333;
}